.page-footer {
  width: 100%;
  margin-top: 40px;
  height: 100vh;
}
.content {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-top: 1px solid #e8e8e8;
  border-bottom: 1px solid #e8e8e8;
  display: flex;
  justify-content: space-between;
}
.content .prev,
.content .next {
  width: 50%;
  padding: 32px 12px;
}
.content .prev .label,
.content .next .label {
  margin-bottom: 8px;
  font-size: 14px;
  color: #bfbfbf;
}
.content .prev .title,
.content .next .title {
  font-size: 16px;
  font-weight: 500;
  color: #262626;
  transition: color 0.35s ease;
}
.content .next {
  text-align: right;
}
